import { Button, Modal, Select, Form, Input } from 'antd'
import { Select as MantineSelect, Text, Avatar, Group } from '@mantine/core'
import "../styles/Modal.css"
import { symbolData, colorData, boardSizeOptions } from '../utils/Constants'
import React, { useRef, useContext } from 'react'
import { GameSettingsContext } from '../contexts/GameSettingsContexts'
import { useNavigate } from 'react-router-dom'

export default function ModalDiv({ open, setOpen }) {

    const [form] = Form.useForm();
    const formRef = useRef(null)
    const navigate = useNavigate()
    const { gameSettings, setGameSettings } = useContext(GameSettingsContext)

    const filteredSymbolData1 = symbolData.filter(a => a.value !== gameSettings.selectedSymbol2)
    const filteredSymbolData2 = symbolData.filter(a => a.value !== gameSettings.selectedSymbol1)

    const filteredColor1 = colorData.filter(a => a.value !== gameSettings.selectedColor2)
    const filteredColor2 = colorData.filter(a => a.value !== gameSettings.selectedColor1)

    const SelectItem = React.forwardRef(({ image, label, description, ...others }, ref) => (
        <div ref={ref} {...others}>
            <Group noWrap>
                <Avatar src={image} />

                <div>
                    <Text size="sm">{label}</Text>
                    <Text size="xs" opacity={0.65}>
                        {description}
                    </Text>
                </div>
            </Group>
        </div>
    ));


    function onFinish(values) {

        setGameSettings({
            ...gameSettings,
            player1Name: values.player1Name,
            player2Name: values.player2Name
        });
        setOpen(false)
        navigate('/game')

    }

    function onReset() {

        formRef.current.resetFields()
        setOpen(false)
    }



    return (
        <div className='modal'>
            <Modal
                open={open}
                onCancel={() => setOpen(false)}
                title="j jtk kezdse"
                centered
                className="centered_modal"
                footer={[
                    <Button
                        danger
                        key="back"
                        size='large'
                        onClick={onReset}
                    >
                        Mgse
                    </Button>,

                    <Button
                        style={{ backgroundColor: '#4BB543', fontWeight: 600 }}
                        danger
                        key="submit"
                        type='primary'
                        size='large'
                        htmlType="submit"
                        onClick={() => form.submit()}
                    >
                        Ltrehoz
                    </Button>
                ]}
            >

                <div className='description'>
                    <p>A jtk kt szerepls, megkezdshez ktelez nevet, illetve szimblumot kivlasztania (minden jtkoshoz csupn egy szimblum tartozhat)! Ha kvnja mdosthatja a kivlasztott szimblum sznt, illetve a plya mrett (alaprtelmezetten minden szimblum fekete szn, illetve a plya mrete egy 10X10-es hl).</p>
                    <p>Akinek elszr sszejn egyms mellett vzszintesen, fgglegesen vagy tlsan 5 jel, az gyztt.</p>
                    <p>J jtkot!</p>
                </div>

                <Form
                    labelCol={{ span: 24, style: { fontWeight: 600 } }}
                    form={form}
                    onFinish={onFinish}
                    ref={formRef}
                >
                    <div style={{ display: 'flex', justifyContent: 'space-around' }}>

                        <div style={{ width: '45%' }}>
                            <Form.Item
                                label="1. jtkos"
                                name="player1Name"
                                rules={[{ required: true, message: 'Krem adjon meg egy nevet!' }]}
                            >

                                < Input
                                    placeholder='Nv'
                                    size='default'
                                    autoComplete='off'
                                />
                            </Form.Item>

                            <Form.Item
                                label="Vlasszon egy szimblumot!"
                                name="selectedSymbol1"
                                rules={[{ required: true, message: 'Krem vlasszon egy szimblumot!' }]}
                            >
                                <Select
                                    placeholder="Vlasszon szimblumot!"
                                    options={filteredSymbolData1}
                                    onChange={(value) => setGameSettings({ ...gameSettings, selectedSymbol1: value })}
                                />
                            </Form.Item>

                            <Form.Item name="selectedColor1" >
                                <MantineSelect
                                    label='Vlaszthat egy sznt!'
                                    placeholder='Vlaszthat egy sznt!'
                                    itemComponent={SelectItem}
                                    data={filteredColor1}
                                    onChange={(value) => setGameSettings({ ...gameSettings, selectedColor1: value })}

                                />
                            </Form.Item>
                        </div>



                        <div style={{ width: '45%' }}>
                            <Form.Item
                                label="2. jtkos"
                                name="player2Name"
                                rules={[{ required: true, message: 'Krem adjon meg egy nevet!' }]}
                            >
                                < Input
                                    placeholder='Nv'
                                    size='default'
                                    autoComplete='off' />
                            </Form.Item>

                            <Form.Item
                                label="Vlasszon egy szimblumot!"
                                name="selectedSymbol2"
                                rules={[{ required: true, message: 'Krem vlasszon egy szimblumot!' }]}
                            >
                                <Select
                                    placeholder="Vlasszon szimblumot!"
                                    options={filteredSymbolData2}
                                    onChange={(value) => setGameSettings({ ...gameSettings, selectedSymbol2: value })}
                                />
                            </Form.Item>

                            <Form.Item name="selectedColor2" >
                                <MantineSelect
                                    label='Vlaszthat egy sznt!'
                                    placeholder='Vlaszthat egy sznt!'
                                    itemComponent={SelectItem}
                                    data={filteredColor2}
                                    onChange={(value) => setGameSettings({ ...gameSettings, selectedColor2: value })}

                                />
                            </Form.Item>
                        </div>
                    </div>

                    <div className='select_wrapper' >
                        <Form.Item
                            label='Vlaszthat hl mretet!'
                            name="selectedboardSize"
                            labelCol={{ span: 24, style: { textAlign: 'center', fontWeight: 600 } }}
                        >

                            <Select
                                placeholder='Vlaszthat hl mretet!'
                                options={boardSizeOptions}
                                onChange={(value) => setGameSettings({ ...gameSettings, selectedBoardSize: value })}
                            />
                        </Form.Item>
                    </div>

                </Form>

            </Modal>
        </div>
    )

}